@import url('https://fonts.googleapis.com/css2?family=Modak&family=Raleway:ital@1&family=Slabo+27px&display=swap');

* {
  font-family: 'Slabo 27px', serif;
  font-size: 30px;
}

body{
  padding: 60px;
}

li {
  font-size: 20px;
}

input {
  color: #a8201a;
  font-size: 0.9rem;
  width: 100%;
}


.card-grid {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.card {
  align-content: center;
  border: 1px solid grey;
  border-radius: 6px;
  overflow: hidden;
  margin-bottom: 10px;
  margin-right: 10px;
}

.card p {
  padding: 5px;
  text-align: center;
}